import { IExposedProperty, RoomType } from '@/models/property';
import { Component, Fragment, h, Listen, Prop } from '@stencil/core';
import { v4 } from 'uuid';

@Component({
  tag: 'ir-property-gallery',
  styleUrl: 'ir-property-gallery.css',
  shadow: true,
})
export class IrPropertyGallery {
  @Prop() exposed_property: IExposedProperty;
  @Prop() property_state: 'carousel' | 'gallery' = 'gallery';
  @Prop() roomType: RoomType;
  private irDialog: HTMLIrDialogElement;

  @Listen('openGallery')
  handleOpenGallery() {
    this.irDialog.openModal();
  }
  @Listen('carouselImageClicked')
  handleOpenCarouselGallery() {
    this.irDialog.openModal();
  }

  render() {
    if (!this.exposed_property) {
      return null;
    }
    const images = this.property_state === 'carousel' ? this.roomType.images : this.exposed_property.images;
    return (
      <div>
        {this.property_state === 'gallery' ? (
          <ir-gallery totalImages={this.exposed_property.images.length} images={this.exposed_property.images?.map(i => i.url).slice(0, 5)}></ir-gallery>
        ) : (
          <Fragment>
            <div class="flex text-gray-700 flex-wrap items-center gap-2 font-normal md:hidden text-sm py-2">
              <ir-accomodations
                bookingAttributes={{
                  max_occupancy: this.roomType.occupancy_max.adult_nbr,
                  bedding_setup: this.roomType.bedding_setup,
                }}
                amenities={this.exposed_property.amenities}
              ></ir-accomodations>
            </div>
            <div class="relative md:hidden w-full h-48 carousel-container   rounded-md">
              {this.roomType.images.length === 1 ? (
                <img
                  onClick={() => this.irDialog.openModal()}
                  class="rounded-[var(--radius,8px)] cursor-pointer w-full h-full object-cover "
                  src={this.roomType.images[0].url}
                  alt={this.roomType.images[0].tooltip}
                />
              ) : (
                <ir-carousel
                  slides={this.roomType?.images?.map(img => ({
                    alt: img.tooltip,
                    id: v4(),
                    image_uri: img.url,
                  }))}
                ></ir-carousel>
              )}
            </div>
            <div class="hidden md:block py-2">
              <div class="w-full relative md:w-auto md:max-h-[150px] mb-1 lg:max-h-[200px] xl:max-h-[250px]  carousel-container   rounded-md">
                {this.roomType.images?.length === 1 ? (
                  <img
                    onClick={() => this.irDialog.openModal()}
                    src={this.roomType.images[0].url}
                    alt={this.roomType.images[0].tooltip}
                    class="w-full h-full cursor-pointer object-cover rounded-[var(--radius,8px)] "
                  />
                ) : (
                  <ir-carousel
                    slides={this.roomType.images?.map(img => ({
                      alt: img.tooltip,
                      id: v4(),
                      image_uri: img.url,
                    }))}
                  ></ir-carousel>
                )}
                <div class="lg:hidden">
                  <ir-accomodations
                    bookingAttributes={{
                      max_occupancy: this.roomType.occupancy_max.adult_nbr,
                      bedding_setup: this.roomType.bedding_setup,
                    }}
                    amenities={this.exposed_property.amenities}
                  ></ir-accomodations>
                </div>
              </div>
              <ir-button onButtonClick={() => this.irDialog.openModal()} variants="ghost" label="More details"></ir-button>
            </div>
          </Fragment>
        )}
        <ir-dialog ref={el => (this.irDialog = el)}>
          <div slot="modal-body" class="modal-container pt-0 px-4 pb-4  md:p-4 md:pt-0">
            <div class=" bg-white sticky top-0 z-50 py-2 md:pt-4  mb-2 flex justify-between items-center w-full">
              <h2 class="font-semibold text-lg md:text-xl">{this.property_state === 'carousel' ? this.roomType.name : this.exposed_property.name}</h2>
              <ir-button variants="icon" onButtonClick={() => this.irDialog.closeModal()}>
                <div slot="btn-icon">
                  <ir-icons name="xmark"></ir-icons>
                </div>
              </ir-button>
            </div>
            <section class="max-h-[80vh]">
              <div class="coursel_gallery_container">
                <ir-carousel
                  slides={images.map(img => ({
                    alt: img.tooltip,
                    id: v4(),
                    image_uri: img.url,
                  }))}
                  onCarouselImageClicked={e => {
                    e.preventDefault();
                    e.stopImmediatePropagation();
                    e.stopPropagation();
                  }}
                ></ir-carousel>
              </div>
              {this.property_state === 'carousel' && (
                <section class={'text-sm mt-4 z-0'}>
                  {/* <ir-facilities properties={this.exposed_property}></ir-facilities> */}
                  <ir-room-type-amenities aminities={this.exposed_property.amenities} roomType={this.roomType}></ir-room-type-amenities>
                  <p innerHTML={this.exposed_property?.description.location_and_intro} class="py-2"></p>
                </section>
              )}
            </section>
          </div>
        </ir-dialog>
      </div>
    );
  }
}
